import React from 'react'
import './style.css'


class VideoPage extends React.Component {
    constructor() {
        super();
        this.onClickHandler = this.onClickHandler.bind(this);
    }

    onClickHandler(event) {
        event.stopPropagation();
    }

    show(event) {
        document.getElementById("lightbox-item").src = event.target.src;
        document.getElementById("lightbox-item").type = event.target.type;
        document.getElementById("lightbox").style.display = "block";
    }

    hide() {
        document.getElementById("lightbox").style.display = "none";
    }

    render() {
        return (
            <div>
                <h2 className="title">Food Videos</h2>
                    <div className="myVideo">
                        <div className="video-container">
                            <video onClick={this.show} src={process.env.PUBLIC_URL + '/videos/wildlife.mp4'} type="video/mp4" autoPlay loop muted></video>
                        </div>
                        <div className="video-container">
                            <video onClick={this.show} src={process.env.PUBLIC_URL + '/videos/wildlife.mp4'} type="video/mp4" autoPlay loop muted></video>
                        </div>
                        <div className="video-container">
                            <video onClick={this.show} src={process.env.PUBLIC_URL + '/videos/wildlife.mp4'} type="video/mp4" autoPlay loop muted></video>
                        </div>
                        <div className="video-container">
                            <video onClick={this.show} src={process.env.PUBLIC_URL + '/videos/wildlife.mp4'} type="video/mp4" autoPlay loop muted></video>
                        </div>
                        <div className="video-container">
                            <video onClick={this.show} src={process.env.PUBLIC_URL + '/videos/wildlife.mp4'} type="video/mp4" autoPlay loop muted></video>
                        </div>
                        <div className="video-container">
                            <video onClick={this.show} src={process.env.PUBLIC_URL + '/videos/wildlife.mp4'} type="video/mp4" autoPlay loop muted></video>
                        </div>
                    </div>
                <div id="lightbox" className="lightbox" onClick={this.hide}>
                    <video id="lightbox-item" className="lightbox-item" onClick={this.onClickHandler} controls></video>
                </div>
            </div>
        );
    }

}

export default VideoPage;