import React from 'react'
import './style.css'

class EmailPage extends React.Component {

    submitEmail() {
        var result = document.getElementById("result");

        if (/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(document.getElementById("email").value)) {
            result.style.color = "green";
            result.innerHTML = "Email successfully recorded!";
        }
        else {
            result.style.color = "red";
            result.innerHTML = "Invalid email address.";
        }
    }

    render() {
        return (
            <div>
                <h2 className="title">Question to Ask?</h2>
                <label>What's your email address?</label><br />
                <input id="email" type="text" />
                <button className="sumbitBtn" onClick={this.submitEmail}>Submit!</button>
                <br />
                <p id="result"></p>
            </div>);
    }
}

export default EmailPage;