import React from 'react'
import './style.css'

class ImagePage extends React.Component {
    constructor() {
        super();
        this.onClickHandler = this.onClickHandler.bind(this);
    }

    onClickHandler(event) {
        event.stopPropagation();
    }

    show(event) {
        document.getElementById("lightbox").style.display = "block";
        document.getElementById("lightbox-item").src = event.target.src;
    }

    hide() {
        document.getElementById("lightbox").style.display = "none";
    }

    render() {
        return (
            <div>
                <h2 class="title">Inspiring Moments</h2>
                <div class="gallery-wrapper" id="gallery">
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img2.jpg'} />
                        </div>
                    </div>
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img3.jpeg'} />
                        </div>
                    </div>
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img4.jpg'} />
                        </div>
                    </div>
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img5.jpeg'} />
                        </div>
                    </div>
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img6.jpg'} />
                        </div>
                    </div>
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img7.jpg'} />
                        </div>
                    </div>
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img8.jpg'} />
                        </div>
                    </div>
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img9.jpg'} />
                        </div>
                    </div>
                    <div class="gallery-item">
                        <div class="image">
                            <img onClick={this.show} src={process.env.PUBLIC_URL + '/images/img10.jpg'} />
                        </div>
                    </div>
                </div>
                <div id="lightbox" className="lightbox" onClick={this.hide}>
                    <img id="lightbox-item" className="lightbox-item" onClick={this.onClickHandler} />
                </div>
            </div>
        );
    }
}

export default ImagePage;
