import React from 'react'
import './style.css';

function Itabs(props){
    const addStyle=()=>{
        if(props.item.id === props.activetab)
            return{backgroundColor:'red',fontWeight: 'bold'}
        else
            return{backgroundColor:'tomato'}
    };

    console.log(props)
    return(
        <div className="nav-item" style={addStyle()} onClick={props.ctab.bind(this,props.item.id)}>{props.item.title}</div>
    );
}

export default Itabs;
