import Body from './component/body'
import Tablist from './component/tablist.js'
import './App.css';
import { Component } from 'react';

class App extends Component {

  constructor() {
    super();
    this.state = {activetab: 0};

    this.changetab = (id) => {
      this.setState({activetab: id});
    }

    this.onScrollHandler = this.onScrollHandler.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.scrollToTop = this.scrollToTop.bind(this);
  }

  /* Source: https://stackoverflow.com/questions/29725828/update-style-of-a-component-onscroll-in-react-js */
  componentDidMount() {
    window.addEventListener('scroll', this.onScrollHandler);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScrollHandler);
  }

  onScrollHandler() {
    this.scroll();
  }

  /* scrollToTop button refer to https://codepen.io/matthewcain/pen/ZepbeR */

  handleScroll() {
    var scrollToTopBtn = document.querySelector(".scrollToTopBtn")
    // var scrollBtn = document.getElementById("backBtn");
    var height = window.innerHeight / 4;

    if(document.body.scrollTop > height || document.documentElement.scrollTop > height) {
      scrollToTopBtn.classList.add("show")
    }
    else {
      scrollToTopBtn.classList.remove("show")
    }
  }

  scrollToTop() {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    this.scroll();
  }

  render() {
    const tabs = [{
      id: 0,
      title: 'Home'
    }, {
      id: 1,
      title: 'Text'
    }, {
      id: 2,
      title: 'Images'
    }, {
      id: 3,
      title: 'Videos'
    }, {
      id: 4,
      title: 'Table'
    }, {
      id: 5,
      title: 'Email'
    }];

    return (
      <div className="App">
        <div className="container">
          <body>
            <div className="nav">
              <div className="nav-bar">
                <Tablist tabs={tabs} activetab={this.state.activetab} ctab={this.changetab} />
              </div>
            </div>
            <div className="main-body">
              <Body activetab={this.state.activetab} />
            </div>
            <a id="backBtn" class="scrollToTopBtn" onClick={this.scrollToTop}></a>
          </body>
        </div>
      </div>
    );
  }
}

export default App;
